import Template from 'components/Common/Template'
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image'
import React, { FunctionComponent } from 'react'
import { PostListItemType } from 'types/PostItem.types'
import { graphql } from 'gatsby'
import Header from 'components/Common/Header'
import styled from '@emotion/styled'
import Title from 'components/Common/Title'
import Particle from 'components/Common/Particle/Particle'

type BlogPageProps = {
  location: {
    search: string
  }
  data: {
    allMarkdownRemark: {
      edges: PostListItemType[]
    }
    file: {
      childImageSharp: {
        profileImage: IGatsbyImageData
      }
    }
  }
}

const blog: FunctionComponent<BlogPageProps> = ({
  location: { search },
  data: {
    allMarkdownRemark: { edges },
    file: {
      childImageSharp: { profileImage },
    },
  },
}) => {
  const InnerBox = styled.div`
    width: 768px;
    margin: 0 auto auto;
    color: white;
    z-index: 10;

    @media (max-width: 768px) {
      width: 100%;
    }
  `
  const ImageWrapper = styled.img`
    width: 500px;
    height: 150px;
  `
  const Description = styled.div`
    font-size: 25px;
    font-weight: 300;
    margin-top: 100px;

    span {
      display: flex;
      margin-bottom: 20px;
    }

    @media (max-width: 768px) {
      font-size: 15px;
      padding: 0 20px;
    }
  `

  return (
    <Template title="Louis's Blog" description=" ">
      <InnerBox>
        <Header></Header>
        <Title
          title={`.`}
          subTitle="louis-25  ."
        />
        {/* <img src={rainbow_cat} alt="" /> */}
        {/* <ImageWrapper
          src="http://vincentgarreau.com/particles.js/assets/img/kbLd9vb_new.gif"
          alt=""
        /> */}
        <Description>
          <span> </span>
          <span>Gaysby</span>
          <span>React</span>
          <span>TypeScript</span>
          <span> </span>
          {/* <ImageWrapper image={profileImage} alt="logo_Image"></ImageWrapper> */}
        </Description>
      </InnerBox>
      <Particle></Particle>
    </Template>
  )
}

export default blog

export const getPostList = graphql`
  query getIndexPost {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date, frontmatter___title] }
    ) {
      edges {
        node {
          id
          fields {
            slug
          }
          frontmatter {
            title
            summary
            date(formatString: "YYYY.MM.DD.")
            categories
            thumbnail {
              childImageSharp {
                gatsbyImageData
              }
            }
          }
        }
      }
    }
    file(name: { eq: "profile-image" }) {
      childImageSharp {
        profileImage: gatsbyImageData(width: 120, height: 120)
      }
    }
  }
`
